import React, { FC, useEffect, useRef } from 'react';
import { notification } from 'antd';
import { courseData } from '../data/courses.ts';  // 

interface AMapProps {
  selectedLocation?: any;
  onMetroStationsFound?: (stations: any[]) => void;
}

declare global {
  interface Window {
    AMap: any;
  }
}

const SEARCH_RADIUS = 10000; //  10 

const AMap: FC<AMapProps> = ({ selectedLocation, onMetroStationsFound }) => {
  const mapRef = useRef<any>(null);
  const placeSearchRef = useRef<any>(null);

  // 
  const searchNearbyMetro = (location: any) => {
    if (!mapRef.current) {
      notification.error({ message: '' });
      return;
    }

    // 
    const searchOptions = {
      city: '',  // 
      citylimit: true, // 
      pageSize: 1,
      pageIndex: 1
    };

    // 
    placeSearchRef.current.setCity('');
    placeSearchRef.current.setCityLimit(true);

    // 
    placeSearchRef.current.search(location.name, (status: string, result: any) => {
      console.log(':', status);
      console.log(':', result);

      if (status === 'complete' && result.poiList?.pois?.length > 0) {
        const location = result.poiList.pois[0];
        
        // 
        mapRef.current?.clearMap();

        // 
        const searchMarkerContent = `
          <div class="search-marker">
            <div class="search-icon">
              <div class="pulse"></div>
            </div>
            <div class="search-name">${location.name}</div>
          </div>
        `;

        new window.AMap.Marker({
          map: mapRef.current,
          position: [location.location.lng, location.location.lat],
          content: searchMarkerContent,
          offset: new window.AMap.Pixel(-15, -30),
          title: location.name
        });

        // 
        const searchStyle = document.createElement('style');
        searchStyle.textContent = `
          .search-marker {
            position: relative;
            cursor: pointer;
          }
          .search-icon {
            width: 24px;
            height: 24px;
            background: #ff4d4f;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            box-shadow: 0 0 8px rgba(255,77,79,0.5);
          }
          .pulse {
            width: 16px;
            height: 16px;
            background: rgba(255,255,255,0.8);
            border-radius: 50%;
            animation: pulse 1.5s infinite;
          }
          .search-name {
            position: absolute;
            white-space: nowrap;
            background: rgba(255,255,255,0.95);
            color: #ff4d4f;
            font-weight: bold;
            padding: 3px 8px;
            border-radius: 2px;
            font-size: 13px;
            top: -28px;
            left: 50%;
            transform: translateX(-50%);
            box-shadow: 0 1px 4px rgba(0,0,0,0.2);
          }
          @keyframes pulse {
            0% {
              transform: scale(0.8);
              opacity: 1;
            }
            50% {
              transform: scale(1);
              opacity: 0.8;
            }
            100% {
              transform: scale(0.8);
              opacity: 1;
            }
          }
        `;
        document.head.appendChild(searchStyle);

        // 
        mapRef.current.setZoomAndCenter(15, [location.location.lng, location.location.lat], false);

        // 
        const nearbySearch = new window.AMap.PlaceSearch({
          type: '',
          pageSize: 50,
          pageIndex: 1,
          radius: SEARCH_RADIUS, // 
          extensions: 'all'
        });

        nearbySearch.searchNearBy(
          '', 
          [location.location.lng, location.location.lat], 
          SEARCH_RADIUS,  // 
          (status: string, result: any) => {
            if (status === 'complete' && result.poiList?.pois?.length > 0) {
              const stations = result.poiList.pois;
              
              // 
              const stationMap = new Map();
              stations.forEach((station: any) => {
                // 
                let cleanName = station.name
                  .replace(/[(]()[)]/g, '')  // 
                  .replace(/.*?[0-9]+/g, '')  // 
                  .replace(/.*?[A-Z]/g, '')     // 
                  .replace(/.*?/g, '')      // 
                  .replace(/.*?[]/g, '') // 
                  .replace(/.*?[0-9]+/g, '') // 
                  .replace(/.*?[0-9]+/g, '') // 
                  .replace(/[\s]/g, '')                       // 
                  .trim();

                // 
                const lines = new Set<string>();
                station.address.split(';').forEach((line: string) => {
                  const match = line.match(/(\d+)/);
                  if (match) {
                    lines.add(match[1]);
                  }
                });

                // ""
                if (!cleanName.includes('')) {
                  cleanName += '';
                }

                // 
                if (!stationMap.has(cleanName) || stationMap.get(cleanName).distance > station.distance) {
                  const distance = window.AMap.GeometryUtil.distance(
                    [location.location.lng, location.location.lat],
                    [station.location.lng, station.location.lat]
                  );

                  // 
                  const courseInfo = courseData.find(c => 
                    c.station.includes(cleanName.replace('', '')) || 
                    cleanName.includes(c.station)
                  );

                  stationMap.set(cleanName, {
                    id: station.id,
                    name: cleanName,
                    address: Array.from(lines).map(line => `${line}`).join(';'),
                    distance: distance,
                    location: station.location,
                    lines: Array.from(lines),
                    courses: courseInfo?.courses || []  // 
                  });
                }
              });

              const uniqueStations = Array.from(stationMap.values());
              console.log(':', uniqueStations);

              // 
              mapRef.current?.clearMap();

              // 
              const searchMarkerContent = `
                <div class="search-marker">
                  <div class="search-icon">
                    <div class="pulse"></div>
                  </div>
                  <div class="search-name">${location.name}</div>
                </div>
              `;

              new window.AMap.Marker({
                map: mapRef.current,
                position: [location.location.lng, location.location.lat],
                content: searchMarkerContent,
                offset: new window.AMap.Pixel(-15, -30),
                title: location.name
              });

              // 
              uniqueStations.forEach((station: any) => {
                const stationMarkerContent = `
                  <div class="metro-marker">
                    <div class="metro-icon">
                      <img src="https://webapi.amap.com/theme/v1.3/markers/n/mark_b.png" />
                    </div>
                    <div class="metro-name">${station.name}</div>
                  </div>
                `;

                new window.AMap.Marker({
                  map: mapRef.current,
                  position: [station.location.lng, station.location.lat],
                  content: stationMarkerContent,
                  offset: new window.AMap.Pixel(-15, -30),
                  title: station.name
                });
              });

              // 
              if (onMetroStationsFound) {
                onMetroStationsFound(uniqueStations);
              }

              mapRef.current?.setFitView();
              notification.success({ message: ` ${uniqueStations.length} ` });
            } else {
              notification.warning({ message: '' });
              if (onMetroStationsFound) {
                onMetroStationsFound([]);
              }
            }
          }
        );

        notification.success({ message: `: ${location.name}` });
      } else {
        notification.warning({ message: '' });
      }
    });
  };

  useEffect(() => {
    const initMap = () => {
      const map = new window.AMap.Map('container', {
        zoom: 11,
        center: [112.982279, 28.19409]
      });
      
      mapRef.current = map;

      map.plugin(['AMap.PlaceSearch'], () => {
        placeSearchRef.current = new window.AMap.PlaceSearch({
          city: ''
        });
      });

      map.plugin(['AMap.ToolBar', 'AMap.Scale'], () => {
        map.addControl(new window.AMap.ToolBar());
        map.addControl(new window.AMap.Scale());
      });
    };

    if (window.AMap) {
      initMap();
    } else {
      const checkAMap = setInterval(() => {
        if (window.AMap) {
          clearInterval(checkAMap);
          initMap();
        }
      }, 100);
    }
  }, []);

  useEffect(() => {
    if (selectedLocation) {
      searchNearbyMetro(selectedLocation);
    }
  }, [selectedLocation]);

  return (
    <div 
      id="container" 
      style={{ 
        height: '500px', 
        width: '100%',
        border: '1px solid #ccc'
      }}
    ></div>
  );
};

export default AMap;